.container {
  max-width: 980px;
  margin: auto;
  padding: 20px;
  grid-gap: 40px;
}

.container .navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.container .navigation h4:first-child {
  width: 70%;
}

.container .grid-2 {
  display: grid;
  margin-left: auto;
  margin-right: auto;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 40px;
}

.container .grid-1-2-1 {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-gap: 40px;
  justify-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.container .card {
  position: relative;
  height: 300px;
  padding: 20px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(196, 141, 141, 0.06), 0 6px 10px 0 rgba(0, 0, 0, 0.06);
          box-shadow: 0 4px 8px 0 rgba(196, 141, 141, 0.06), 0 6px 10px 0 rgba(0, 0, 0, 0.06);
  background-color: #E6E2DE;
  -webkit-transition: -webkit-transform .2s ease;
  transition: -webkit-transform .2s ease;
  transition: transform .2s ease;
  transition: transform .2s ease, -webkit-transform .2s ease;
}

.container .card a {
  display: block;
  z-index: 2;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.container .card .card-text {
  height: 100%;
  width: 80%;
  z-index: 1;
  opacity: 0;
  -webkit-transition: opacity .4s ease;
  transition: opacity .4s ease;
}

.container .card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.container .card:hover .card-text {
  opacity: 0.85;
}

.container .card:hover .card-background {
  opacity: 0.10;
}

.container .card a {
  color: #1F454E;
}

.container .card .card-background {
  max-width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #E6E2DE;
  opacity: 0.95;
  background-position: center center;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: 0;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

